.containerStyle {
    width: 50%;
    margin: 0 auto;
    border: 1px solid #ccc;
    border-radius: 5px;
    background-color: #f0f0f0;
    text-align: center;
    padding: 20px;
    display: flex;
    flex-direction: column; /* 세로 방향 컨테이너 */
    justify-content: flex-start; /* 상단 정렬 */
}

.leftContent {
    width: 100%; /* 왼쪽 50% 너비 설정 */
    text-align: left; /* 텍스트 왼쪽 정렬 */
}

.rightContent {
    width: 100%; /* 오른쪽 50% 너비 설정 */
    text-align: right; /* 텍스트 오른쪽 정렬 */
}

.Content {
    display: flex;
    width: 100%;
    justify-content: space-between;
    align-items: flex-start;
}

.mainContent {
    width: 100%;
    text-align: center; /* 텍스트 가운데 정렬 */
}

.chatButton {
    background-color: #0074cc;
    color: #fff;
    padding: 15px 15px;
    border: none;
    border-radius: 5px;
    cursor: pointer;
    margin: 0 auto;
    display: block;
}
